<template>
  <div
    style="text-align: center;line-height:100px;width:500px;heigth:200px;margin:50px 0px 100px 0px;font-size:30px; "
  >
    <div v-for="(n,index) in ex" :key=index>
      <p>
        {{index+1}}. {{n.name}}
        <v-list-item-action>
          <v-btn :class="n.fav ? 'red--text' : ''" icon @click="likeFav(n.fav,index)">
            <v-icon>mdi-heart</v-icon>
          </v-btn>
        </v-list-item-action>
      </p>
      like:{{like[index]}}
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      ex: [
        { name: "菜單名", fav: false },
        { name: "菜單名", fav: false },
        { name: "菜單名", fav: false },
        { name: "菜單名", fav: false },
        { name: "菜單名", fav: false },
        { name: "菜單名", fav: false }
      ],
      like:[0,0,0,0,0,0]
    };
  },
  methods: {
    likeFav(fav,index) {//計算喜歡~因不是存取數據庫刷新歸0,無法紀錄
      console.log("index:"+index)
      this.ex[index].fav = !fav;
      if(this.ex[index].fav){
        this.$store.commit("snackbar/setAddMenuLike",index);
        this.updata(index);
      }else{
        this.$store.commit("snackbar/setLessMenuLike",index);
      }
      //this.$store.commit("snackbar/increment");
      this.like[index]=this.$store.state.snackbar.menuLike[index];
    },
    updata(index){
      axios
      .post("http://localhost:8081/likeMenu/menu",
        this.$qs.stringify({'index':index})
      )
      .then(response=>{
        console.log(response.data);
        this.like[index]=response.data;
      })
      .catch(error=>{console.log(error)})
    }
  }
};
</script>
