            ex:[{name:'致詞'},{name:'表演',ex:'文字內容',exing:false},{name:'用餐'},{name:'表演',ex:'文字內容',exing:false},{name:'抽獎'},{name:'表演',ex:'文字內容',exing:false},{name:'用餐'},{name:'抽獎'},{name:'合照'}]
        }
    },
